html {
  font-family: Roboto, sans-serif;
}

body {
  --main-color: #00acdc;
  margin: 0;
}

a {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  margin-top: 10px;
}

.menu {
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu > li:not(:last-child) {
  margin-right: 20px;
}

.menu-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: flex;
  position: relative;
}

.menu-link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

